<template>
	<div class="boxProse">
		<div class="imgBox">
			<p class="title">审核资料</p>
			<el-upload
					class="avatar-uploader"
					action=""
					:show-file-list="false"
					accept=".jpg,.jpeg,.png,.gif,.bmp,.JPG,.JPEG,.PNG,.GIF,.BMP"
					:http-request="uploadUsersIDImgA"
					:before-upload="beforeUpload">
				<img v-if="UsersIDImgA" :src="isGetlocation+UsersIDImgA" class="avatar">
				<i v-else class="el-icon-plus avatar-uploader-icon"></i>
			</el-upload>
      <el-upload
					class="avatar-uploader"
					action=""
					:show-file-list="false"
					accept=".jpg,.jpeg,.png,.gif,.bmp,.JPG,.JPEG,.PNG,.GIF,.BMP"
					:http-request="uploadUsersIDImgB"
					:before-upload="beforeUpload">
				<img v-if="UsersIDImgB" :src="isGetlocation+UsersIDImgB" class="avatar">
				<i v-else class="el-icon-plus avatar-uploader-icon"></i>
			</el-upload>
      <el-upload
					class="avatar-uploader"
					action=""
					:show-file-list="false"
					accept=".jpg,.jpeg,.png,.gif,.bmp,.JPG,.JPEG,.PNG,.GIF,.BMP"
					:http-request="uploadUsersIDImgC"
					:before-upload="beforeUpload">
				<img v-if="UsersIDImgC" :src="isGetlocation+UsersIDImgC" class="avatar">
				<i v-else class="el-icon-plus avatar-uploader-icon"></i>
			</el-upload>
      <el-upload
					class="avatar-uploader"
					action=""
					:show-file-list="false"
					accept=".jpg,.jpeg,.png,.gif,.bmp,.JPG,.JPEG,.PNG,.GIF,.BMP"
					:http-request="uploadUsersIDImgD"
					:before-upload="beforeUpload">
				<img v-if="UsersIDImgD" :src="isGetlocation+UsersIDImgD" class="avatar">
				<i v-else class="el-icon-plus avatar-uploader-icon"></i>
			</el-upload>
      <el-upload
					class="avatar-uploader"
					action=""
					:show-file-list="false"
					accept=".jpg,.jpeg,.png,.gif,.bmp,.JPG,.JPEG,.PNG,.GIF,.BMP"
					:http-request="uploadUsersIDImgE"
					:before-upload="beforeUpload">
				<img v-if="UsersIDImgE" :src="isGetlocation+UsersIDImgE" class="avatar">
				<i v-else class="el-icon-plus avatar-uploader-icon"></i>
			</el-upload>
		</div>
		<div style="clear: both"></div>
		<el-button style="margin-top: 30px;" size="small" type="success" @click="submitUpdate">提交审核</el-button>
    <div>请注意提交审核资料</div>
    <basic></basic>
	</div>
</template>

<script>
	import {mapState, mapMutations} from 'vuex'
  import basic from'./basic'
	export default {
    name: "check",
    components:{basic},
		data() {
			return {
				dialogImageUrl: "",
				dialogVisible: false,
				allowType: ['gif', 'jpg', 'bmp', 'jpeg', 'png'],
				UsersImage: null,
				UsersIDImgA: null,
				UsersIDImgB: null,
        UsersIDImgC: null,
        UsersIDImgD: null,
				UsersIDImgE: null,
				// imgBase: 'http://lelshopTest.moguxia.cn',
			};
    },
		computed: {
			 userInfo(){
          return this.$store.getters.userInfoS;
        },
        isGetlocation(){
      return  this.$store.getters.isGetlocation;
    }
		},
		mounted() {
			this.UsersIDImgA = this.userInfo.Suppliers_ImgA;
			this.UsersIDImgB = this.userInfo.Suppliers_ImgB;
			this.UsersIDImgC = this.userInfo.Suppliers_ImgC;
      this.UsersIDImgD = this.userInfo.Suppliers_ImgD;
      this.UsersIDImgE = this.userInfo.Suppliers_ImgE;
		},
		methods: {
			...mapMutations(['SET_USERINFO']),
			beforeUpload(file) {
				let typeLimit = false;
				this.allowType.forEach(item => {
					typeLimit = typeLimit || file.type === `image/${item}`;
				});
				const sizeLimit = file.size / 1024 / 1024 < 2;

				if (!typeLimit) {
					this.$Message.error('只支持gif,jpg,bmp,jpeg,png格式图片，请重试');
				} else
				if (!sizeLimit) {
					this.$Message.error('上传图片大小不能超过2MB，请重试');
				}

				return typeLimit && sizeLimit;
			},



			async uploadUsersIDImgA(option) {
				await this.uploadImg(option, 'UsersIDImgA');
			},

			async uploadUsersIDImgB(option) {
				await this.uploadImg(option, 'UsersIDImgB');
			},

			async uploadUsersIDImgC(option) {
				await this.uploadImg(option, 'UsersIDImgC');
			},

      	async uploadUsersIDImgD(option) {
				await this.uploadImg(option, 'UsersIDImgD');
      },
      	async uploadUsersIDImgE(option) {
				await this.uploadImg(option, 'UsersIDImgE');
			},
			async uploadImg(option, target) {
				let res = await this.$request.uploadImg(option.file);
				if (res) {
					this.$Message.success('上传成功');
					this[`${target}`] =res.content;
				} else {
					this.$Message.error('上传失败');
				}
			},

			async submitUpdate() {
				let userInfo = JSON.parse(localStorage.getItem('userInfoS'));
				userInfo.Code = "";
        userInfo.Msg = "";
			  userInfo.Suppliers_ImgA=this.UsersIDImgA;
			  userInfo.Suppliers_ImgB=this.UsersIDImgB;
			  userInfo.Suppliers_ImgC=this.UsersIDImgC;
        userInfo.Suppliers_ImgD=this.UsersIDImgD;
        userInfo.Suppliers_ImgE=this.UsersIDImgE;
				let res = await this.$request.updateUserInfo(userInfo);
				if (res) {
					let tmp =userInfo;
					this.SET_USERINFO(tmp);
					this.$Message.success('提交成功,请耐心等待审核通过');
				} else {
					this.$Message.error('提交失败');
				}
			},
		}
	};
</script>

<style scoped>
	.imgBox {
		float: left;
		margin-right: 80px;
	}
  .imgBox>div{
    margin-left: 10px;
    float: left;
  }
	.title {
		font-size: 16px;
		font-weight: bold;
		margin: 10px 0;
	}

	.avatar-uploader {
		height: 180px;
		width: 180px;
		border: 1px dashed #d9d9d9;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
	}

	.avatar-uploader:hover {
		border-color: #409EFF;
	}

	.avatar-uploader-icon {
		font-size: 28px;
		color: #8c939d;
		width: 178px;
		height: 178px;
		line-height: 178px;
		text-align: center;
	}

	.avatar {
		width: 178px;
		height: 178px;
		display: block;
	}
  .boxProse{
    padding:15px;
  }
</style>



