<template>
  <div>
    <div class="add-container">

      <div class="add-title">
        <h1>供货商信息</h1>
      </div>
      <div class="imgBox">
			<p class="title">头像</p>
			<el-upload
					class="avatar-uploader"
					action=""
					:show-file-list="false"
					accept=".jpg,.jpeg,.png,.gif,.bmp,.JPG,.JPEG,.PNG,.GIF,.BMP"
					:http-request="uploadUsersImage"
					:before-upload="beforeUpload">
				<img v-if="UsersImage" :src="isGetlocation+UsersImage" class="avatar">
				<i v-else class="el-icon-plus avatar-uploader-icon"></i>
			</el-upload>
      <!-- <el-button size="small" type="success" @click="submitUpdate" class="submitImg">提交</el-button> -->
		</div>
      <div class="add-box">
        <Form
          ref="formInfo"
          :model="formData"
          label-position="left"
          :label-width="100"
          :rules="ruleInline"
        >
          <FormItem label="供应商名称" prop="shopName">
            <i-input v-model="formData.shopName" size="large"></i-input>
          </FormItem>
          <FormItem label="负责人" prop="ownerName">
            <i-input v-model="formData.ownerName" size="large"></i-input>
          </FormItem>
          <FormItem label="联系电话" prop="phone">
            <!-- <i-input v-model="formData.phone" size="large" :disabled="true"></i-input> -->
            <span style="font-size: 15px;">{{formData.phone}}</span>
          </FormItem>
          <FormItem label="供应商地址" prop="address">
            <i-input v-model="formData.address" size="large"></i-input>
          </FormItem>
          <FormItem label="邮箱" prop="email">
            <i-input v-model="formData.email" size="large"></i-input>
          </FormItem>
          <FormItem label="身份证号码" prop="IDCardNo">
            <i-input v-model="formData.IDCardNo" size="large"></i-input>
          </FormItem>
          <FormItem label="营业执照号" prop="BusinessNo">
            <i-input v-model="formData.BusinessNo" size="large"></i-input>
          </FormItem>
        </Form>
      </div>
      <div class="add-submit">
        <Button type="primary" @click="submitInfo">确认修改</Button>
      </div>
    </div>

    <div class="add-container">
      <div class="add-title">
        <h1>修改密码</h1>
      </div>
      <div class="add-box">
        <Form
          ref="formPwd"
          :model="formValidate"
          :rules="ruleValidate"
          label-position="left"
          :label-width="100"
        >
          <FormItem label="旧密码" prop="oldPassword">
            <i-input
              type="password"
              v-model="formValidate.oldPassword"
              clearable
              size="large"
              placeholder="请输入旧密码"
            ></i-input>
          </FormItem>
          <FormItem label="新密码" prop="password">
            <i-input
              type="password"
              v-model="formValidate.password"
              clearable
              size="large"
              placeholder="请输入新密码"
            ></i-input>
          </FormItem>
          <FormItem label="确认新密码" prop="rePassword">
            <i-input
              type="password"
              v-model="formValidate.rePassword"
              clearable
              size="large"
              placeholder="请再次输入新密码"
            ></i-input>
          </FormItem>
        </Form>
      </div>
      <div class="add-submit">
        <Button type="primary" @click="submitPwd">确认修改</Button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations,mapActions  } from "vuex";
import { checkEmail,inputTrim } from "@/utils/validate";
export default {
  name: "basic",
  data() {
    const validatePassCheck = (rule, value, callback) => {
      if (value !== this.formValidate.password) {
        callback(new Error("两次输入的密码不一样"));
      } else {
        callback();
      }
    };
    const validIDcardNO=(rule,value,callback)=>{
      var reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
      if(reg.test(value)){
        callback();
      }else{
        callback("身份证号有误")
      }
    }
    return {
      formData: {
        shopName: "",
        ownerName: "",
        phone: "",
        address: "",
        email: "",
        IDCardNo:"",
        BusinessNo:"",
      },
      UsersImage: null,
          allowType: ['gif', 'jpg', 'bmp', 'jpeg', 'png'],
          // imgBase: 'http://lelshopTest.moguxia.cn',
      ruleInline: {
        shopName: [
          { required: true, message: "请输入供货店名称", trigger: "blur",validator:inputTrim }
        ],
        ownerName: [
          { required: true, message: "请输入负责人姓名", trigger: "blur",validator:inputTrim }
        ],
        address: [
          { required: true, message: "请输入店铺地址", trigger: "blur",validator:inputTrim }
        ],
        // phone: [
        //   { required: true, message: "手机号不能为空", trigger: "blur" },
        //   {
        //     type: "string",
        //     pattern: /^1[3|4|5|7|8][0-9]{9}$/,
        //     message: "手机号格式出错",
        //     trigger: "blur"
        //   }
        // ],
       email: [{ validator: checkEmail, trigger: "blur" }],
       IDCardNo:[
          { required: true,validator: validIDcardNO, trigger: "blur" }
       ],
       BusinessNo:[{required: true, message: "请输入营业执照", trigger: "blur"}],
      },
      formValidate: {
        oldPassword: "",
        password: "",
        rePassword: ""
      },
      ruleValidate: {
        oldPassword: [
          { required: true, message: "旧密码不能为空", trigger: "blur",validator:inputTrim },
          {
            type: "string",
            min: 6,
            message: "密码长度不能小于6",
            trigger: "blur"
          }
        ],
        password: [
          { required: true, message: "新密码不能为空", trigger: "blur",validator:inputTrim },
          {
            type: "string",
            min: 6,
            message: "密码长度不能小于6",
            trigger: "blur"
          }
        ],
        rePassword: [
          { required: true, message: "请再次输入新密码", trigger: "blur" },
          { validator: validatePassCheck, trigger: "blur" }
        ]
      }
    };
  },

  computed: {
    userInfo() {
      return this.$store.getters.userInfoS;
    },
    isGetlocation(){
      return  this.$store.getters.isGetlocation;
    }
  },
  mounted() {
    this.UsersImage = this.userInfo.Suppliers_HeadImage;
    this.formData.shopName = this.userInfo.Suppliers_Name;
    this.formData.ownerName = this.userInfo.Suppliers_ResponPeople;
    this.formData.phone = this.userInfo.Suppliers_MobilePhone;
    this.formData.address = this.userInfo.Suppliers_Addr;
    this.formData.email = this.userInfo.Suppliers_Email;
    this.formData.IDCardNo = this.userInfo.IDCardNo;
    this.formData.BusinessNo = this.userInfo.BusinessNo;
  },
  created() {},
  methods: {
    ...mapMutations(["SET_USERINFO"]),
    ...mapActions(["LogOut"]),
    beforeUpload(file) {
				let typeLimit = false;
				this.allowType.forEach(item => {
					typeLimit = typeLimit || file.type === `image/${item}`;
				});
				const sizeLimit = file.size / 1024 / 1024 < 2;
				if (!typeLimit) {
					this.$Message.error('只支持gif,jpg,bmp,jpeg,png格式图片，请重试');
				} else
				if (!sizeLimit) {
					this.$Message.error('上传图片大小不能超过2MB，请重试');
				}
				return typeLimit && sizeLimit;
      },
    async submitUpdate(){
      let userInfo=JSON.parse(localStorage.getItem('userInfoS'));
         userInfo.Suppliers_HeadImage=this.UsersImage;
         let res = await this.$request.updateUserInfo(userInfo);
         if (res) {
					let tmp =userInfo;
					this.SET_USERINFO(tmp);
          this.$Message.success('提交成功');
          this.$router.go();
				} else {
					this.$Message.error('提交失败');
				}
      },
    submitInfo() {
      this.$refs["formInfo"].validate(async valid => {
        if (valid) {
          let userInfo = JSON.parse(localStorage.getItem("userInfoS"));
          userInfo.Code = "";
          userInfo.Msg = "";
          userInfo.Suppliers_HeadImage=this.UsersImage;
          userInfo.Suppliers_Name = this.formData.shopName;
          userInfo.Suppliers_ResponPeople = this.formData.ownerName;
          userInfo.Suppliers_MobilePhone = this.formData.phone;
          userInfo.Suppliers_Addr = this.formData.address;
          userInfo.Suppliers_Email = this.formData.email;
          userInfo.IDCardNo = this.formData.IDCardNo;
          userInfo.BusinessNo = this.formData.BusinessNo;
          let res = await this.$request.updateUserInfo(userInfo);
          if (res&&res.code==0) {
            let tmp = userInfo;
            this.SET_USERINFO(tmp);
            this.$Message.success("修改成功");
            let timEOUT=setTimeout(() => {
               this.$router.go();
               clearTimeout(timEOUT)
            }, 1000);
          } else {
            this.$Message.error("修改失败");
          }
        } else {
          this.$Message.error("请填写正确的信息");
        }
      });
    },
    	async uploadUsersImage(option) {
				await this.uploadImg(option, 'UsersImage');
			},
    submitPwd() {
      this.$refs["formPwd"].validate(async valid => {
        if (valid) {
          let userInfo = JSON.parse(localStorage.getItem("userInfoS"));
          let res = await this.$request.updatePwd(
            userInfo.Suppliers_MobilePhone,
            this.formValidate.oldPassword,
            this.formValidate.password
          );
          if (res) {
            this.$Message.success("修改成功");
            this.LogOut();
            this.$router.push({path:"/"})
          } else {
            this.$Message.error("修改失败");
          }
        } else {
          this.$Message.error("请填写正确的信息");
        }
      });
    },
    async uploadImg(option, target) {
				let res = await this.$request.uploadImg(option.file);
				if (res) {
					this.$Message.success('上传成功');
					this[`${target}`] = res.content;
				} else {
					this.$Message.error('上传失败');
				}
			},
  }
};
</script>

<style scoped>
.add-container {
  margin: 15px auto 120px auto;
  width: 60%;
  min-width: 600px;
}
.submitImg{
  margin-left: 10px;
}
.add-title {
  margin-bottom: 15px;
  text-align: center;
}

.add-submit {
  display: flex;
  justify-content: center;
}
.imgBox {
		margin-right: 80px;
    margin-bottom: 30px;
	}
  .imgBox::after{
    content: "";
    display: block;
    clear: both;
  }
  .imgBox>div{
    float: left;

  }
	.title {
		font-size: 16px;
		font-weight: bold;
		margin: 10px 0;
	}

	.avatar-uploader {
		height: 180px;
		width: 180px;
		border: 1px dashed #d9d9d9;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
	}

	.avatar-uploader:hover {
		border-color: #409EFF;
	}

	.avatar-uploader-icon {
		font-size: 28px;
		color: #8c939d;
		width: 178px;
		height: 178px;
		line-height: 178px;
		text-align: center;
	}

	.avatar {
		width: 178px;
		height: 178px;
		display: block;
	}
  .boxProse{
    padding:15px;
  }
</style>



